<template>
	<view class="shop-list">
		<view class="shop-title f-color">
			<view class="shop-item" v-for="(item,index) in shopList.data" :key="index" @tap="changeTab(index)">
				<view :class="shopList.currentIndex == index ? 'f-active-color':''">{{item.name}}</view>
				<view class="shop-icon">
					<view class="iconfont icon-shangjiantou up" :class="item.status == 1?'f-active-color':''"></view>
					<view class="iconfont icon-xiajiantou down" :class="item.status == 2?'f-active-color':''"></view>
				</view>
			</view>
		</view>
		<Lines></Lines>
		<CommodityList :dataList="dataList"></CommodityList>	
	</view>
</template>

<script>
	import Lines from "@/components/common/Line.vue"
	import CommodityList from "@/components/common/CommodityList.vue"
	export default{
		data(){
			return{
				shopList:{
					currentIndex:0,
					data:[
						{name:"价格",status:1},
						{name:"折扣",status:0},
						{name:"品牌",status:0}
					]
				},
				
				dataList:[
							{
								id:1,
								imgUrl:"../../static/img/shop1.jpg",
								name:"大衣绒毛",
								pprice:"299",
								oprice:"599",
								descode:"5.2折"
							},
							{
								id:2,
								imgUrl:"../../static/img/shop2.jpg",
								name:"大衣绒毛",
								pprice:"299",
								oprice:"599",
								descode:"5.2折"
							},
							{
								id:3,
								imgUrl:"../../static/img/shop3.jpg",
								name:"大衣绒毛",
								pprice:"299",
								oprice:"599",
								descode:"5.2折"
							},
							{
								id:4,
								imgUrl:"../../static/img/shop4.jpg",
								name:"大衣绒毛",
								pprice:"299",
								oprice:"599",
								descode:"5.2折"
							},
						]
			   }
		},
		methods:{
			changeTab(index){
				// 索引值
				let idx = this.shopList.currentIndex
				// 具体那一个对象
				let item = this.shopList.data[idx]
				if(idx == index){
					return item.status = item.status === 1 ? 2 : 1
				}
				// 新的值
				let newitem = this.shopList.data[index];
				item.status = 0;
				this.shopList.currentIndex = index;
				newitem.status = 1
				
				this.shopList.currentIndex= index
			}
		},
		
		components:{
			Lines,
			CommodityList
		}
	}
</script>

<style lang="scss" scoped>
.shop-title{
	display: flex;
	.shop-item{
		flex: 1;
		display: flex;
		justify-content: center;
		align-items: center;
		height: 80rpx;
		.shop-icon{
			position: relative;
			margin-left: 8rpx;
			margin-bottom: 16rpx;
			.iconfont{
				width: 16rpx;
				height: 5rpx;
				margin-bottom: 8rpx;
			}
		}
	}
	
}
	
</style>